<template>
	<div class="userMedia">
		<div class="cover" @click.self='hide' v-show="showCover"></div>
		<div class="btns" :class="{show: showBtns}">
			<!-- <div class="btn" @click="getAuthority">拍照</div> -->
			<div class="btn">
				从相册中选择
				<label for="imgfileinput" class="inputfile"></label>
				<input hidden id="imgfileinput" type="file" accept="image/jpeg, image/jpg, image/gif, image/png, image/bng" @change="changeImg($event)">
			</div>
			<div class="btn cancel" @click='hide'>取消</div>
		</div>

		<div class="videoBox" :class='{showVideo: showVideo}' ref='videoBox'>
			<div class="video-transform">
				<video ref='video'></video>
			</div>
		</div>
	</div>
</template>

<script>
(function () {
  if (navigator.mediaDevices == void (0)) {
    navigator.mediaDevices = {}
  }
  if (navigator.mediaDevices.getUserMedia == void (0)) {
    navigator.mediaDevices.getUserMedia = (constraints) => {
      let getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia
      if (!getUserMedia) {
        return Promise.reject(new Error('getUserMedia is not exact in this browser'))
      }
      return new Promise((resolve, reject) => {
        getUserMedia.call(navigator, constraints, resolve, reject)
      })
    }
  };
})()
export default {
  props: {
    show: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      showBtns: false,
      showCover: false,
      showVideo: false
    }
  },
  watch: {
    show: {
      handler (val) {
        if (val) {
          this.showCover = true
          this.showBtns = true
        } else {
          setTimeout(() => {
            this.showCover = false
          }, 200)
          this.showBtns = false
        }
      },
      immediate: true
    }
  },
  methods: {
    hide () {
      this.$emit('hide')
    },
    getAuthority () {
      var exArray = []; var that = this
      if (this.$refs.videoBox.requestFullScreen) {
        this.$refs.videoBox.requestFullScreen()
      } if (this.$refs.videoBox.webkitRequestFullScreen) {
        this.$refs.videoBox.webkitRequestFullScreen()
      } if (this.$refs.videoBox.mozRequestFullScreen) {
        this.$refs.videoBox.mozRequestFullScreen()
      } if (this.$refs.videoBox.msRequestFullscreen) {
        this.$refs.videoBox.msRequestFullscreen()
      }
      // 				ele.webkitRequestFullScreen && ele.webkitRequestFullScreen()
      // 				ele.mozRequestFullScreen && ele.mozRequestFullScreen()
      // 				ele.msRequestFullscreen && ele.msRequestFullscreen()
      navigator.mediaDevices.enumerateDevices()
        .then((devices) => {
          devices.forEach((device) => {
            console.log(device.kind + ': ' + device.label +
								' id = ' + device.deviceId)
            if (device.kind == 'videoinput') {
              exArray.push(device.deviceId)
              console.log(device.deviceId)
            }
          })
          console.log(222222222222)
          window.navigator.mediaDevices.getUserMedia({
            audio: true,
            video: {
              width: 1080,
              height: 1920,
              deviceId: {
                exact: exArray[1]
              }
            }
          })
            .then(steam => {
              let video = this.$refs.video
              if ('srcObject' in video) {
                video.srcObject = steam
              } else {
                window.URL.createObjectURL(steam)
              }
              video.onloadedmetadata = () => {
                video.play()
              }
              that.useCamera()
            }).catch((e) => {
              console.log('console', e)
            })
          console.log('console', 11212121121212)
        })
    },
    useCamera () {
      console.log('console', '步骤调整video元素')
      if (this.showVideo) return
      if (window.location.hash) {
        window.onpopstate = state => {
          this.showVideo = false
          this.exitScreen()
          window.onpopstate = null
        }
        window.history.pushState({
          camera: true
        }, null, '/camera' + window.location.hash)
        this.showVideo = true
      } else {
        window.onhashchange = () => {
          this.showVideo = location.hash.split('/').reverse()[0] == 'camera'
          !this.showVideo && (window.onhashchange = null)
          this.showVideo ? '' : this.exitScreen()
        }
        location.hash = location.hash + '/camera'
      }
    },
    exitScreen () {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen()
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen()
      }
    },
    changeImg ($event) {
      console.log('console', $event.target)
      let fileX = $event.target
      if (fileX && fileX.files) {
        let file = fileX.files[0]
        if (file) {
          this.$emit('handlechangeimg',
            file
          )
          // fileX.files[0] = null;
          fileX.value = null
          this.hide()
        } else {
          console.log('clearFile')
        }
      }
    }
  },
  mounted () {

  }
}
</script>

<style lang="scss" scoped="scoped">
	@import '@/assets/styles/mixin.scss';

	.userMedia {
		.cover {
			z-index: 19951105;
			position: fixed;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: rgba($color: #1C1D1D, $alpha: 0.7);
		}

		.btns {
			z-index: 19951106;
			position: fixed;
			top: 100%;
			left: 0;
			right: 0;
			transform: translateY(0);
			transition: transform .2s linear;

			&.show {
				transform: translateY(-100%);
			}

			.btn {
				height: pxToRem(98px);
				font-size: pxToRem(34px);
				background: #FFFFFF;
				color: #1C1D1D;
				text-align: center;
				position: relative;
				line-height: pxToRem(98px);
				&:not(:last-of-type) {
					border-bottom: 1px solid rgba($color: #1C1D1D, $alpha: 0.7);
				}
				&.cancel {
					margin-top: pxToRem(30px);
				}
				.inputfile{
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					right: 0;
					z-index: 1;
				}
			}
		}

		.videoBox {
			position: fixed;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background: #000;
			z-index: 19951107;
			display: none;
			.video-transform{
				height: 100%;
				width: 100%;
				transform-style: preserve-3d;
				background: #000;
				transform-origin: center center;
				z-index: 19951105;
				display: none;
				video {
					position: absolute;
					height: 100%;
					width: 100%;
				}
			}
			&.showVideo {
				display: block;
				.video-transform {
					animation: video .3s;
					display: block;
				}
			}
		}
		@keyframes video {
			from {
				transform: translateX(-100%)
			}
			to {
				transform: translateX(0)
			}
		}
	}
</style>
