<template>
	<div class="upload">
		<template v-if="true">
			<div class="ctx">
				<div class="msg">
					<div class="order_status clearfix">
						<div class="order fl">
							理赔申请编号：{{data.sn}}
						</div>
						<div class="status fr">
							{{data.status | claimStatus}}
						</div>
					</div>
					<div class="name">
						{{data.refer_name}}
					</div>
				</div>
				<div class="up-list">
					<div class="up-item" v-for="(item,index) in data.claim_record_data">
						<div class="title">{{`${index+1}.${item.name}${item.isNecessary ? '(可选)':''}`}}
							<img class="i-help fr" v-if="item.status=='unpassed'" src="@/assets/images/icon_explain.png" @click.stop="handleShowReason(index)">
							<span class="status fr" v-if="item.status=='passed'||item.status=='unpassed'" :class="{unpassed: item.status=='unpassed'}">{{item.status | recordStatus}}</span>
							<div class="reason" :class="{show_reason: show_reason[index]}">
								{{item.reason}}
							</div>
						</div>
						<div class="ttl">{{item.tip}}</div>
						<div class="img-list clearfix">
							<div class="img-item" v-if="item.url">
								<img :src="getImgUrl(item)" @load='imgStyle($event)' @click="show_Bigimg(item)" />
								<span class="deleteImg" @click="handledeleteImg(item)" v-if="item.status!='passed'"></span>
							</div>
							<div class="img-item nullImg" @click="showCamera(item)" v-else></div>
						</div>
						<div class="billNumber" v-if="item.url && item.isBillNumber">
							<span v-if="item.billNumber">业务流水号：{{item.billNumber}}</span>
							<span class="red" v-else>未填写业务流水号</span>
							<span class="fr btn" @click="showBigimg(item)">{{item.billNumber ? '修改' : '填写'}}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="foot">
				<div class="fbtn save" @click="saveAndSubmit(0)">保存</div>
				<div class="fbtn up" @click="saveAndSubmit(1)">提交</div>
			</div>
			<UserMedia :show='camera' @hide='hideCamera' @handlechangeimg='handleChange'></UserMedia>
			<div class="fullScreen" v-if="fullImg.show" @click.self="closeFullImg">
				<mHeader :title="'修改业务流水号'"></mHeader>
				<div class="returnBtn" @click="$router.back()"></div>
				<div class="imgWrap">
					<img :src="getImgUrl(fullImg)" @load='_imgStyle($event)' @click.self="closeFullImg">
				</div>
				<div class="billinput" v-if="fullImg.isBillNumber"><span class="txt">业务流水号：</span><input type="text" v-model="fullImg.billNumber" />
					<span class="ok" @click="handlechangebill">确定</span>
				</div>
			</div>
			<div class="fullScreen" v-if="full_Img.show" @click="_closeFullImg">
				<img :src="getImgUrl(full_Img)" @load='_imgStyle($event)'>
			</div>
		</template>


		<template v-else>
			<div class="upload">
				<div class="ctx">
					<div class="msg">
						<div class="order_status clearfix">
							<div class="order fl">
								理赔申请编号：{{data.sn}}
							</div>
							<div class="status fr">
								{{data.status | claimStatus}}
							</div>
						</div>
						<div class="name">
							{{data.refer_name}}
						</div>
					</div>
					<div class="tip">
						<div class="ttl">你需要准备以下材料至离您最近的

							营业厅办理理赔资料提交：<br />
							<br />
							1.理赔申请书<br />
							2.被保险人(出险人)的身份证明<br />
							3.门(急)诊病例<br />
							4.医疗费用收据原件<br />
							5.费用清单(处方)原件<br />
							<span class="gay">如已在其他单位报销，可提供复印件和费用分割单原件</span><br />
							6.意外事故说明<br />
							7.存折首页复印件(选交)<br />
							<br />
							获取最近营业厅或咨询替他问题拨打
							的客服热线：
							<br />
						</div>
						<div class="okbtn" @click="toClaimList">确 定</div>
					</div>
				</div>
			</div>
		</template>
	</div>
</template>

<script>
	import mHeader from '@/components/base/mHeader.vue'
	import axios from 'axios';
	import UserMedia from '@/components/claim/userMedia.vue';
	window.data_copy = {}
	export default {
		data() {
			return {
				camera: false,
				data: {},
				activeFileWrap: null, //最近被点击的图片添加框
				files: [],
				fullImg: {},
				//单纯展示图片
				full_Img: {},
				show_reason: {}
			}
		},
		methods: {
			toClaimList() {
				this.$router.push('/claimlist')
			},
			hideCamera() {
				this.camera = false
			},
			showCamera(item) {
				this.activeFileWrap = item;
				this.camera = true
			},
			handleChange(file) {
				let URL = window.URL || window.webkitURL;
				this.activeFileWrap.url = URL.createObjectURL(file);
				data_copy.claim_record_data.find((c) => {
					return c.claimDataConfigId == this.activeFileWrap.claimDataConfigId
				}).url = URL.createObjectURL(file);
				this.data.claim_record_data.find((c) => {
					return c.claimDataConfigId == this.activeFileWrap.claimDataConfigId
				}).status = 'uploaded'
				data_copy.claim_record_data.find((c) => {
					return c.claimDataConfigId == this.activeFileWrap.claimDataConfigId
				}).status = 'uploaded'
				this.files.push({
					claimDataConfigId: this.activeFileWrap.claimDataConfigId,
					file: file
				})
			},
			handledeleteImg(item) {
				item.url = null;
				let claim_record_data_item = data_copy.claim_record_data.find((cla, index) => cla.claimDataConfigId == item.claimDataConfigId);
				claim_record_data_item.url = '';
				if (claim_record_data_item.billNumber) {
					claim_record_data_item.billNumber = '';
				}
				this.files.splice(this.files.findIndex((file, index) => file.claimDataConfigId == item.claimDataConfigId), 1)
			},
			//尺寸
			imgStyle(event) {
				let img = event.target;
				let shape = img.naturalWidth / img.naturalHeight;
				if (shape > 1) {
					img.style.width = '100%';
					img.style.height = 'auto'
				} else {
					img.style.height = '100%';
					img.style.width = 'auto'
				}
			},
			//大屏尺寸
			_imgStyle(event) {
				let img = event.target;
				let shape = img.naturalWidth / img.naturalHeight;
				if (shape > img.parentElement.clientWidth / img.parentElement.clientHeight) {
					img.style.width = '100%';
					img.style.height = 'auto'
				} else {
					img.style.height = '100%';
					img.style.width = 'auto'
				}
			},
			getImgUrl(item) {
				return item.url.indexOf('blob:http') > -1 ? item.url : process.env.VUE_APP_RESOURCE_URL + item.url
			},
			showBigimg(item) {
				this.fullImg = {
					show: true,
					url: item.url,
					claimDataConfigId: item.claimDataConfigId,
					billNumber: item.billNumber,
					isBillNumber: item.isBillNumber
				}
			},
			show_Bigimg(item) {
				this.full_Img = {
					show: true,
					url: item.url,
				}
			},
			closeFullImg() {
				this.fullImg = {}
			},
			_closeFullImg() {
				this.full_Img = {}
			},
			handlechangebill() {
				data_copy.claim_record_data.find((claim, index) => claim.claimDataConfigId == this.fullImg.claimDataConfigId)
					.billNumber = this.fullImg.billNumber;
				this.data.claim_record_data.find((claim, index) => claim.claimDataConfigId == this.fullImg.claimDataConfigId)
					.billNumber = this.fullImg.billNumber;
				this.closeFullImg()
			},
			handleShowReason(index) {
				this.$set(this.show_reason, index, !this.show_reason[index])
			},
			upload() {
				return new Promise((resolve, reject) => {
					this.files.forEach((file, index) => {
						let formData = new FormData;
						formData.append('file', file.file)
						axios.post('/common/file/upload',
								formData, {
									headers: {
										'Content-Type': 'multipart/form-data'
									}
								})
							.then(res => {
								let data = res.data;
								data_copy.claim_record_data.find(c => file.claimDataConfigId == c.claimDataConfigId).url = (data.data &&
									data.data.filePath) || '';
								index + 1 == this.files.length && resolve();
							})
							.catch((e) => reject(e))
					});
					if (!this.files.length) {
						resolve()
					}
				})
			},
			saveAndSubmit(status) {
				if (status == 1 && !this.verify()) return
				this.upload().then(() => {
					this.$api.cla.submit({
						claimRecordId: data_copy.id,
						claimRecordDatas: JSON.stringify(data_copy.claim_record_data),
						status: status
					}).then((res) => {
						this.$toast.center(res.message)
						if (status == 1 && res.code == 1) {
							this.$router.push('/claimUpload/success/' + res.data)
						}
					})
				})
			},
			verify() {
				let res = true;
				data_copy.claim_record_data.forEach((claim, i) => {
					//isNecessary代表非必需 !isNecessary是必填项
					if (!claim.isNecessary) {
						if (!claim.url) {
							res = false;
							this.$toast.center('请先上传完整资料后再提交')
						}
					}
				});
				res && data_copy.claim_record_data.forEach((claim, i) => {
					if (claim.url && !!claim.isBillNumber && !claim.billNumber) {
						res = false;
						this.$toast.center('请先填写票据业务流水号')
					}
				});
				return res;
			}
		},
		components: {
			UserMedia,
			mHeader
		},
		mounted() {
			this.$api.cla.getRecord({
				sn: this.$route.query.claimApplySn
			}).then((res) => {
				if (res.code == 1) {
					this.data = JSON.parse(JSON.stringify(res.data || '[]'));
					data_copy = JSON.parse(JSON.stringify(res.data || '[]'));
				} else {
					console.error('getData fail');
				}
			});
			document.addEventListener('click',()=>{
				if(this.show_reason){
					this.handleShowReason();
				}
			})
		}
	}
</script>

<style lang="scss" scoped="scoped">
	@import '@/assets/styles/mixin.scss';

	.upload {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: pxToRem(140px);
		background: #FFF;
		overflow-y: auto;

		.ctx {
			font-size: pxToRem(30px);

			.msg {
				background: #EDEDED;
				padding: pxToRem(20px) pxToRem(30px) pxToRem(19px);

				.order_status {
					color: #A5A8A8;
					line-height: 1;
					margin-bottom: pxToRem(19px);
				}

				.name {
					font-size: pxToRem(36px);
					line-height: 1.1;
					color: #1C1D1D;
				}
			}

			.up-list {
				background: #FFFFFF;

				.up-item {
					padding: pxToRem(30px);

					.title {
						line-height: pxToRem(30px);
						color: #1C1D1D;
						margin-bottom: pxToRem(25px);
						position: relative;

						.status {
							color: #A5A8A8;

							&.unpassed {
								color: #FF4B4B;
							}
						}

						.i-help {
							margin: pxToRem(-10px) 0;
							width: pxToRem(50px);
							height: pxToRem(50px);
							margin-left: pxToRem(10px);
						}

						.reason {
							padding: pxToRem(20px);
							max-width: pxToRem(600px);
							line-height: 1.2;
							position: absolute;
							top: pxToRem(50px);
							white-space: normal;
							word-break: break-all;
							right: 0;
							border: pxToRem(2px) solid #EDEDED;
							box-shadow: 0 pxToRem(6px) pxToRem(12px) 0 rgba(182, 187, 187, 0.45);
							z-index: -1;
							height: 0;
							opacity: 0;
							transition: opacity .5s;
							background: #FFF;

							&:after {
								display: block;
								position: absolute;
								border-width: 0 pxToRem(18px) pxToRem(15px);
								content: '';
								border-style: solid;
								border-color: transparent transparent #EDEDED;
								right: pxToRem(8px);
								top: pxToRem(-15px);
								z-index: 19951105;
							}

							&:before {
								display: block;
								position: absolute;
								border-width: 0 pxToRem(18px) pxToRem(15px);
								content: '';
								border-style: solid;
								border-color: transparent transparent #fff;
								right: pxToRem(8px);
								top: pxToRem(-10px);
								z-index: 19951106;
							}

							&.show_reason {
								z-index: 19951105;
								height: auto;
								opacity: 1;
							}
						}
					}

					.ttl {
						font-size: pxToRem(26px);
						margin-bottom: pxToRem(12px);
						color: #B6BBBB;
						line-height: 1.2;
					}

					.img-list {
						.img-item {
							position: relative;
							float: left;
							margin-right: pxToRem(10px);
							border: pxToRem(2px) solid #E6E9E9;
							border-radius: pxToRem(4px);
							width: pxToRem(180px);
							height: pxToRem(180px);
							box-sizing: border-box;
							background: #ffffff;

							img {
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate3d(-50%, -50%, 0);
							}

							&.nullImg:after {
								position: absolute;
								transform: translate3d(-50%, -50%, 0);
								top: 50%;
								left: 50%;
								width: pxToRem(48px);
								height: 2px;
								background: #D5D8D8;
								z-index: 1;
								content: '';
								box-sizing: border-box;
							}

							&.nullImg:before {
								box-sizing: border-box;
								content: '';
								background: #D5D8D8;
								position: absolute;
								transform: translate3d(-50%, -50%, 0);
								top: 50%;
								left: 50%;
								width: 2px;
								z-index: 1;
								height: pxToRem(48px);
							}
						}

						.deleteImg {
							position: absolute;
							left: 100%;
							bottom: 100%;
							transform: translate3d(-50%, 65%, 0);
							width: pxToRem(44px);
							height: pxToRem(44px);
							background: #FF4B4B;
							border-radius: 50%;
							color: #FFFFFF;
							font-weight: 400;
							text-align: center;

							&:after {
								content: '';
								width: pxToRem(20px);
								height: pxToRem(4px);
								background: #fff;
								position: absolute;
								top: pxToRem(20px);
								left: pxToRem(12px);
								z-index: 19951105;
							}
						}
					}

					.billNumber {
						font-size: pxToRem(26px);
						color: #B6BBBB;
						line-height: 1;
						margin-top: pxToRem(27px);

						.red {
							color: red;
						}

						.btn {
							border: 1px solid #B6BBBB;
							color: #B6BBBB;
							line-height: pxToRem(30px);
							display: inline-block;
							padding: pxToRem(15px);
							margin-top: pxToRem(-15px);
							width: pxToRem(90px);
							text-align: center;
							border-radius: pxToRem(30px);
							font-size: pxToRem(30px);

						}
					}
				}
			}

			.tip {
				.ttl {
					font-size: pxToRem(30px);
					color: #1C1D1D;
					line-height: pxToRem(48px);
					padding: pxToRem(30px);

					.gay {
						font-size: pxToRem(25px);
						color: #A5A8A8;
						padding-left: pxToRem(50px);
					}
				}

				.okbtn {
					background: #1CD7D1;
					box-shadow: 0 pxToRem(12px) pxToRem(20px) 0 #D5D8D8;
					border-radius: pxToRem(51px);
					text-align: center;
					font-size: pxToRem(34px);
					color: #fff;
					line-height: pxToRem(90px);
					width: pxToRem(630px);
					margin: 0 auto;
				}
			}
		}

		.fullScreen {
			position: fixed;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background: #ffffff;
			z-index: 19951105;

			.imgWrap {
				position: absolute;
				bottom: pxToRem(80px);
				top: pxToRem(84px);
				left: 0;
				right: 0;
			}

				img {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate3d(-50%, -50%, 0);
				}

			.billinput {
				padding: 0 pxToRem(190px) 0 pxToRem(210px);
				margin: 10px 0;
				box-sizing: border-box;
				position: relative;
				font-size: pxToRem(30px);
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 1;

				.txt {
					color: #1C1D1D;
					line-height: pxToRem(80px);
					position: absolute;
					left: pxToRem(30px);
					top: pxToRem(24px);
					line-height: pxToRem(30px);
				}

				input {
					line-height: pxToRem(30px);
					background: #FFFFFF;
					padding: pxToRem(24px) pxToRem(20px);
					width: 100%;
					box-sizing: border-box;
					border: 1px solid #D5D8D8;
				}

				.ok {
					position: absolute;
					right: pxToRem(30px);
					top: 0;
					height: pxToRem(80px);
					border: 1px solid #1CD7D1;
					line-height: pxToRem(78px);
					text-align: center;
					color: #FFFFFF;
					width: pxToRem(130px);
					background-color: #1CD7D1;
					border-radius: pxToRem(40px);
				}
			}

			.returnBtn {
				width: pxToRem(168px);
				height: pxToRem(84px);
				position: absolute;
				top: 0;
				left: 0;
				z-index: 19951105;
			}
		}

		.foot {
			position: fixed;
			bottom: 0;
			left: 0;
			right: 0;
			padding: pxToRem(30px) pxToRem(60px) pxToRem(30px) pxToRem(90px);
